
    import { defineComponent } from 'vue';

    export default defineComponent({
        name: 'ToDoListForm',
        data: () => {
            return {
                input: '',
            };
        },
        methods: {
            addTask(): void {
                this.$emit('addTask', this.input);
                this.input = '';
            },
            deleteAllTasks(): void {
                this.$emit('deleteAllTasks');
            },
        },
    });
    